import Vue from 'vue'
// import Router from 'vue-router'
import Login from '@/components/login.vue'
import { IonicVueRouter } from '@ionic/vue'

Vue.use(IonicVueRouter)

export default new IonicVueRouter({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: Login
    }
  ]
})



